/* TODO:  BEM all the things, then Sass all the things.  */
.content-header {
  margin-bottom: 15px; }

.section-title {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-size: 24px;
  color: #8FCED2;
  padding: 10px 0;
  background-color: #3C4573; }

.section-image {
  width: 100%; }

.page-header {
  position: relative;
  margin-bottom: 15px; }

/* remove styles at breakpoint */
.page-header > .page-header-text {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5em;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  padding: 8px 0;
  color: #3C4573; }

.page-header > .page-header-text > .page-dropdown {
  position: relative;
  top: 15px;
  left: 10px;
  border-width: 7px 5px; }

.page-header > .page-header-text > .page-dropdown.arrow-down {
  border-top-color: #3C4573; }

.page-header > .page-header-text > .page-dropdown.arrow-up {
  border-bottom-color: #3C4573; }

/* display at breakpoint */
.page-header > .question {
  display: none; }

.filters > .order {
  display: block;
  margin-bottom: 15px; }

.sidebar {
  display: none; }

.filters {
  margin-bottom: 10px; }

/* Profile contents */
.profile-zone {
  border-top: 1px solid black;
  padding-top: 15px; }

.profiles {
  list-style: none;
  font-size: 0px;
  padding: 0; }

/* override at breakpoint */
.profiles > li:nth-child(odd) {
  margin-right: 3%; }

.profile {
  display: inline-block;
  width: 48.5%;
  background: #fff;
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
  font-size: 16px;
  font-size: 1rem;
  vertical-align: top;
  box-shadow: 0 0 5px #ddd;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  margin-bottom: 20px;
  cursor: pointer; }

.profile img {
  max-width: 100%;
  height: auto; }

.profile .details {
  padding: 10px;
  background-color: #fffac2;
  margin-top: -1px;
  color: #1B229D;
  height: 151px; }

.profile .details > span {
  max-width: 180px; }

.profile .details > .name {
  display: block;
  font-size: 2em;
  font-style: italic; }

.profile .details > .age {
  display: block;
  margin: 5px 0 10px;
  font-size: 1.3em;
  color: #66584D; }

.profile .deatils .view-profile {
  font-size: 1.2em; }

@media all and (min-width: 440px) {
  .profile {
    width: 31.33333333%;
    margin-right: 3%; }
  .profiles > li:nth-child(3n) {
    margin-right: 0; } }

@media all and (min-width: 660px) {
  .content-header {
    background-color: #8FCED6; }
  .section-title {
    display: inline-block;
    text-align: center;
    text-transform: none;
    font-size: 24px;
    color: inherit;
    padding: 0;
    background-color: inherit;
    width: 40%; }
  .section-image {
    display: inline-block;
    vertical-align: middle;
    width: 59.4%; }
  .page-header > .page-header-text {
    display: block;
    text-align: center;
    text-transform: uppercase;
    font-size: 1.5em;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    padding: 8px 0;
    color: #3C4573; }
  .sidebar {
    display: inline-block;
    width: 20%;
    vertical-align: top;
    padding-right: 10px; }
  .section-menu {
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    padding: 10px;
    margin-bottom: 15px; }
  .section-menu li {
    padding: 5px; }
  .section-menu li > a {
    color: #5A4D29;
    font-size: 1.2em; }
  .callout {
    border-right: 1px solid black;
    border-bottom: 1px solid black; }
  .callout > .description {
    display: block;
    background-color: #56B8D0;
    padding: 10px;
    font-style: italic;
    font-size: 1.5em;
    color: #3D3811;
    line-height: 1.2em; }
  .callout-image {
    width: 100%; }
  .callout-controls {
    display: block;
    background-color: #56B8D0;
    padding: 10px;
    color: #3D3811;
    margin-top: -2px; }
  .callout-type {
    display: block;
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 5px;
    line-height: 1.2em; }
  .callout-title {
    font-style: italic;
    font-size: 1.5em;
    line-height: 1.2em;
    margin-bottom: 5px; }
  .main-column {
    display: inline-block;
    width: 75%;
    padding-left: 10px; } }

/* small adjustment to ensure image stays snugly against the right side of the header */
@media all and (min-width: 775px) {
  .section-image {
    width: 59.5%; } }

@media all and (min-width: 880px) {
  .profile {
    width: 22.75%;
    margin-right: 3%; }
  .profiles > li:nth-child(3n) {
    margin-right: 3%; }
  .profiles > li:nth-child(4n) {
    margin-right: 0; } }

/* small adjustment to ensure image stays snugly against the right side of the header */
@media all and (min-width: 965px) {
  .section-image {
    width: 59.6%; } }
